.best-deals {
  background-color: rgba(26, 153, 134, 0.9);
  padding: 8em 0 4em; }

.deal-card {
  padding-right: 1em; }
  .deal-card__container {
    position: relative; }
  .deal-card__overlay {
    background-color: rgba(0, 0, 0, 0.25);
    display: flex;
    flex-direction: column;
    padding: 2em 1.5em;
    position: absolute;
    height: 100%;
    justify-content: space-between;
    left: 0;
    top: 0;
    width: 100%; }
    .deal-card__overlay p {
      color: #c7ebe8; }

@media screen and (max-width: 736px) {
  .deal-card__overlay {
    padding: 1em; }
    .deal-card__overlay * {
      line-height: 1.5;
      margin: 0; }
    .deal-card__overlay h3 {
      font-size: 10pt; }
    .deal-card__overlay p {
      font-size: 9.5pt; } }



